import { useState } from "react";
import styles from "./Signup.module.sass";
import { Link, useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { FaUserAlt } from "react-icons/fa";
import { BsPhoneFill } from "react-icons/bs";
import { AiTwotoneLock, AiOutlineMail } from "react-icons/ai";

import * as yup from "yup";
import Background from "../Background/Background";
import signup from "../../api/signup";
import check_user from "../../api/check_user";
import forgot_password from "../../api/forgot_password";
import { Button } from "react-bootstrap";
import confirm_code from "../../api/confirm_code";
import { useEffect } from "react";
import { WrongCode } from "../ForgotPassword/ForgotPassword";
import { useSnackbar } from "notistack";
import validatePhoneNumber from "../../util/validatePhone";
import validateEmail from "../../util/validateEmail";
import validatePassword from "../../util/validatePassword";
import validateConfirmPassword from "../../util/validateConfirmPassword";
import { BiShowAlt, BiHide } from "react-icons/bi"
// eslint-disable-next-line

const Signup = () => {
  // eslint-disable-next-line
  // const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();
  const [username, setUsername] = useState(() => "");
  const [phoneNumber, setPhoneNumber] = useState(() => "");
  const [password, setPassword] = useState(() => "");
  const [email, setEmail] = useState(() => "");
  // eslint-disable-next-line
  const [confirmPassword, setConfirmPassword] = useState(() => "");
  // eslint-disable-next-line
  const [data, setData] = useState();

  const [message, setMessage] = useState();
  const [checkFinal, setCheckFinal] = useState();
  const [codeVerify, setCodeVerify] = useState();
  const [status, setStatus] = useState();
  const [showPassword, setShowPassword]= useState(false)
  const [showConfirmPassowrd, setShowConfirmPassword]= useState(false)
  const checkUserandSignup = async () => {
    const check = await check_user(email, phoneNumber);
    if (check?.msg === true) {
      setMessage(true);
      forgot_password(email, setCheckFinal, true);
    } else {
      setMessage(false);
    }
  };

  return (
    <div className={styles.container}>
      <div className={styles.register}>
        {
          <>
            <div className={styles.register_title} style={{ marginBottom: 20, fontSize: 18 }}>
              ng k ti khon tin nhn nhanh <br></br> kt ni vi ng dng tin nhn nhanh
            </div>
            <form action="#">
              {message === true && (
                <>
                  {checkFinal && (
                    <>
                      <div style={{ textAlign: "center", fontSize: 12 }}>
                        Chng ti  gi mt m xc thc gm 6 ch s n email
                        ca bn. Vui lng nhp vo y  hon tt qu trnh
                        ng k
                      </div>
                      <input
                        onChange={(e) => setCodeVerify(e.target.value)}
                        type="text"
                        placeholder="Nhp m xc thc"
                      ></input>
                      <br />
                      <Button
                        color={"primary"}
                        onClick={() =>
                          confirm_code(email, codeVerify, setStatus)
                        }
                      >
                        Xc nhn
                      </Button>
                      <WrongCode status={status} />
                      {status?.verify === true && (
                        <AutoSignup
                          username={username}
                          phoneNumber={phoneNumber}
                          password={password}
                          email={email}
                          setData={setData}
                        />
                      )}
                    </>
                  )}
                </>
              )}
              {!message && (
                <>
                  <div className={styles.register_form_input}>
                    <input
                      onChange={(e) => setUsername(e.target.value)}
                      type="text"
                      placeholder="H v tn"
                    ></input>
                    <span>
                      {" "}
                      <FaUserAlt />
                    </span>
                  </div>
                  <div className={styles.register_form_input}>
                    <input
                      onChange={(e) => setPhoneNumber(e.target.value)}
                      type="text"
                      placeholder="S in thoi"
                    ></input>
                    <span>
                      <BsPhoneFill />
                    </span>
                  </div>
                  <div className={styles.register_form_input}>
                    <input
                      onChange={(e) => setEmail(e.target.value)}
                      type="email"
                      placeholder="Email"
                    ></input>
                    <span>
                      <AiOutlineMail />
                    </span>
                  </div>
                  <div style={{fontSize: 12, textAlign: "left"}}>Mt khu phi c t nht 8 k t bao gm t nht 1 ch s, 1 ch thng, 1 ch vit hoa v 1 k t c bit</div>
                  <div style={{position: "relative"}} className={styles.register_form_input}>
                    <input
                      type={showPassword=== true ? "text" : "password"}
                      placeholder="Mt khu"
                      required
                      onChange={(e) => setPassword(e.target.value)}
                    ></input>
                    <span>
                      {" "}
                      <AiTwotoneLock />
                    </span>
                    <span style={{position: "absolute", left: "100%", top: "50%", transform: "translate(-100%, -50%)"}}>
                  {
                    showPassword=== false && <BiHide onClick={()=> setShowPassword(true)} />
                  }
                  {
                    showPassword=== true && 
                    <BiShowAlt onClick={()=> setShowPassword(false)} />
                  }

                </span>
                  </div>
                  <div className={styles.register_form_input}>
                    <input
                      type={showConfirmPassowrd=== true ? "text" : "password"}
                      placeholder="Nhp li mt khu"
                      required
                      onChange={(e) => setConfirmPassword(e.target.value)}
                    ></input>
                    <span>
                      {" "}
                      <AiTwotoneLock />
                    </span>
                    <span style={{position: "absolute", left: "100%", top: "50%", transform: "translate(-100%, -50%)"}}>
                  {
                    showConfirmPassowrd=== false && <BiHide onClick={()=> setShowConfirmPassword(true)} />
                  }
                  {
                    showConfirmPassowrd=== true && 
                    <BiShowAlt onClick={()=> setShowConfirmPassword(false)} />
                  }

                </span>
                  </div>
                  <div>
                    {message === false && (
                      <div style={{ color: "red", fontSize: 12 }}>
                        S in thoi hoc gmail  tn ti vui lng th li
                      </div>
                    )}
                  </div>
                  <button
                    onClick={() => {
                      if (username?.length <= 6) {
                        enqueueSnackbar("H tn t nht phi c 6 k t", {
                          variant: "error",
                        });
                      }
                      
                       else if (validatePhoneNumber(phoneNumber) === false) {
                          enqueueSnackbar(
                            "S in thoi khng hp l, phi c 10 ch s",
                            {
                              variant: "error",
                            }
                          );
                      }
                      else if(validateEmail(email) === false ) {
                        enqueueSnackbar(
                            "Email khng hp l",
                            {
                              variant: "error",
                            }
                          );
                      }
                      else if(validatePassword(password) === false ) {
                        enqueueSnackbar(
                            "Mt khu phi c t nht 8 k t, t nht mt ch thng, mt ch hoa v mt ch s",
                            {
                              variant: "error",
                            }
                          );
                      }
                      else if(validateConfirmPassword(password, confirmPassword)=== false ) {
                        enqueueSnackbar(
                            "Mt khu khng khp",
                            {
                              variant: "error",
                            }
                          );
                      }
                      else {
                        checkUserandSignup();
                      }
                    }}
                    className={styles.btn}
                  >
                    ng k
                  </button>
                </>
              )}

              <div className={styles.toLogin}>
                <Link style={{fontSize: 16}} to="/login">ng nhp!</Link>
              </div>
            </form>
          </>
        }
      </div>
      <Background />
    </div>
  );
};

export default Signup;

const AutoSignup = (props) => {
  const navigate = useNavigate();
  useEffect(() => {
    if (props) {
      (async () => {
        await signup(
          props?.username,
          props?.phoneNumber,
          props?.password,
          props?.email,
          props?.setData
        );
        navigate("/login");
      })();
    }
  }, [navigate, props]);
  return <></>;
};
