export default function validatePassword(password) {
    // Khai bo mt bin regex kim tra mt khu
    const passwordRegex = /^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{6,16}$/;
    
    // S dng method test  kim tra mt khu
    if (passwordRegex.test(password)) {
      return true;
    } else {
      return false;
    }
  }