import React, { useState } from "react";
import { BsPhoneFill, BsCode } from "react-icons/bs";
import { useNavigate } from "react-router-dom";
import Background from "../Background/Background";
import styles from "../Signup/Signup.module.sass";
import { Button } from "react-bootstrap";
import forgot_password from "../../api/forgot_password";
import "./style.sass";
import confirm_code from "../../api/confirm_code";
import { AiTwotoneLock } from "react-icons/ai";
import reset_password from "../../api/reset_password";
import swal from "sweetalert";
import { useSnackbar } from "notistack";
import validatePassword from "../../util/validatePassword";
import { memo } from "react";

const ForgotPassword = (props) => {
  // eslint-disable-next-line
  const navigate = useNavigate();
  const {enqueueSnackbar }= useSnackbar()

  const [result, setResult] = useState();
  const [confirmCode, setConfirmCode] = useState();

  const [phoneNumber, setPhoneNumber] = useState(() => "");
  const [status, setStatus] = useState();
  const [password, setPassword] = useState();
  // eslint-disable-next-line
  const [confirmPassword, setConfirmPassword] = useState();

  return (
    <div className={styles.container}>
      <div className={styles.register}>
        {
          <>
            <div
              className={styles.register_title}
              style={{ marginBottom: 20, fontSize: 24 }}
            >
              Qun mt khu <br></br>
            </div>
            <form action="#">
              {!result && (
                <>
                  <div className={styles.register_form_input}>
                    <input
                      type="email"
                      placeholder="Nhp email ca bn  khi phc mt khu"
                      onChange={(e) => setPhoneNumber(e.target.value)}
                      autoComplete={"off"}
                    ></input>
                    <span>
                      <BsPhoneFill />
                    </span>
                  </div>
                  <div className={"fjkadjksjksjdasaa"}>
                    <Button
                      onClick={() => {
                        if(phoneNumber?.trim()?.length <= 0) {
                          return swal("Thng bo", "Bn phi in email  tip tc", "error")
                        }
                        forgot_password(phoneNumber, setResult)
                      }}
                    >
                      Gi
                    </Button>
                  </div>
                </>
              )}
              {result && (
                <div className={"gjldajfskdjsjsa"}>
                  <div className={"jksdjklsdjkasasas"} style={{ fontSize: 12 }}>
                    {result?.message}
                  </div>
                  <div className={styles.register_form_input}>
                    <input
                      type="text"
                      placeholder="Nhp m xc thc  khi phc mt khu"
                      onChange={(e) => setConfirmCode(e.target.value)}
                      autoComplete={"off"}
                    ></input>
                    <span>
                      <BsCode />
                    </span>
                  </div>
                  <div
                    className={`${styles.register_form_input} fjkadjksjksjdasaa`}
                  >
                    <Button
                      onClick={() =>
                        confirm_code(phoneNumber, confirmCode, setStatus)
                      }
                      color={"primary"}
                    >
                      Gi
                    </Button>
                  </div>
                  {status?.verify === true && (
                    <>
                      <div style={{fontSize: 12, textAlign: "left"}}>Mt khu phi c t nht 8 k t gm c 1 ch s, 1 ch vit hoa, 1 ch vit thng v 1 k t c bit</div>
                      <div className={styles.register_form_input}>
                        <input
                          type="password"
                          placeholder="Mt khu"
                          required
                          onChange={(e) => setPassword(e.target.value)}
                        ></input>
                        <span>
                          {" "}
                          <AiTwotoneLock />
                        </span>
                      </div>
                      <div className={styles.register_form_input}>
                        <input
                          type="password"
                          placeholder="Nhp li mt khu"
                          required
                          onChange={(e) => setConfirmPassword(e.target.value)}
                        ></input>
                        <span>
                          {" "}
                          <AiTwotoneLock />
                        </span>
                      </div>
                      <div className={"fjkadjksjksjdasaa"}>
                        <Button
                          onClick={async () =>
                            {
                              if(password?.trim()?.length <= 0) {
                                enqueueSnackbar("Khng c  trng mt khu", {
                                  variant: "error"
                                })
                              }
                              else if(password !== confirmPassword) {
                                  enqueueSnackbar("Xc thc mt khu khng khp", {
                                    variant: "error"
                                  })
                                  return
                              }
                              else if(validatePassword(password?.trim())=== false) {
                                  enqueueSnackbar("Mt khu qu yu, mt khu phi c t nht 8 k t, t nht mt ch thng, mt ch hoa v mt ch s", {
                                    variant: "error"
                                  })
                                  return 
                              }
                              await reset_password(phoneNumber, password, navigate)
                              swal("Thng bo", "Bn  cp nht mt khu thnh cng", "success")
                              .then(()=> navigate("/login"))
                            }

                          }
                          className={"fjkadjksjksjdasaa"}
                          color={"primary"}
                        >
                          Cp nht
                        </Button>
                      </div>
                    </>
                  )}
                  <WrongCode status={status} />
                </div>
              )}
            </form>
          </>
        }
      </div>
      <Background />
    </div>
  );
};

export const WrongCode= memo(({status})=> {
  const {enqueueSnackbar }= useSnackbar()

  return (
    <div style={{display: "none"}}>
      {status?.verify === false && enqueueSnackbar("M xc thc khng chnh xc", {
        variant: "error"
      })}
    </div>
  )
})

export default ForgotPassword;
