export default function validatePhoneNumber(phoneNumber) {
    // Khai bo bin regex kim tra s in thoi
    const phoneRegex = /^(\+?\d{1,3}[- ]?)?\d{10}$/;
    
    // S dng method test  kim tra s in thoi
    if (phoneRegex.test(phoneNumber)) {
      return true;
    } else {
      return false;
    }
  }